body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    background-color: #eff3f7;
    height: 100%;
}

html,
body,
#root,
.container {
    height: 100%;
    padding: 0 10px;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.movies {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 50px;
    row-gap: 70px;
    padding-top: 80px;
}

.movies .movie {
    display: flex;
    justify-content: space-between;
    background-color: white;
    font-weight: 300;
    width: 100%;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__poster {
    width: 30%;
    margin-right: 30px;
    padding-left: 20px;
    position: relative;
}

.movie__data {
    width: 70%;
    margin-bottom: 10px;
    margin-right: 10px;
}

.movie__poster img {
    max-width: 160px;
    position: absolute;
    top: -50px;
}


/* .movie img {
    max-width: 120px;
    margin-right: 30px;
    padding-left: 20px;
    position: absolute;
} */

.movie h3,
.movie h5 {
    font-weight: 300;
}

.movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie__genres,
.movie .movie__year,
.movie__summary {
    color: #b2b3bc;
}

.movie__genres {
    list-style-type: none;
    display: flex;
    padding: 0;
    margin-right: 5px 0px;
}

.genres li {
    margin-right: 10px;
    font-size: 14px;
}